import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PurchasesettingsComponent } from './purchasesettings/purchasesettings.component';
import { SalessettingsComponent } from './salessettings/salessettings.component';

const routes: Routes = [
  {
    path: 'salessettings',
    component: SalessettingsComponent,
    },
    {
      path: 'purchasesettings',
      component: PurchasesettingsComponent,
      },
];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SettingsRoutingModule { }
